* {
	font-family: "NS", sans-serif;
}

a {
	color: #666;
	cursor: pointer;
}

#root {
	height: 100%;
}

.bounce {
	position: relative;
	-webkit-animation: bounce 0.5s linear;
	animation: bounce 0.5s linear;
	width: auto;
	height: auto;
	transition: 0.2s;
	z-index: 99;
	width: 35px;
	height: 35px;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
}

@-webkit-keyframes bounce {
	0% {
		top: 0;
	}
	50% {
		top: -10px;
	}
	100% {
		top: 0;
	}
}

@keyframes bounce {
	0% {
		top: 0;
	}
	50% {
		top: -10px;
	}
	100% {
		top: 0;
	}
}

.infoContainer {
	position: relative;
	bottom: 0;
	text-align: left;
	font-size: 12px;
	line-height: 1.5;
	font-weight: 300;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	float: left;
	height: 50px;
}

.hospitalContainer {
	position: absolute;
	text-align: left;
	overflow: hidden;
	font-size: 13px;
	line-height: 1.5;
	z-index: 99;
	color: #fff;
	height: auto;
	bottom: -8px;
	opacity: 0.8;
}

.infoContents {
	border-radius: 4px;
	overflow: hidden;
}

.hospitalYeoshin {
	border-top: 1px solid #fff;
	padding: 6px 8px;
	text-align: center;
	color: #fff;
	background-color: #ef4b81;
	font-weight: 400;
	font-size: 12px;
	min-width: 100px;
}

.hospitalGeneral {
	border-top: 1px solid #fff;
	padding: 6px 8px;
	text-align: center;
	background-color: #3d3d3d;
	font-weight: 400;
	font-size: 12px;
	min-width: 100px;
}

.hospitalGeneral:first-child,
.hospitalYeoshin:first-child {
	border: 0;
}

.hospitalList:first-child {
	border: none;
}

.point {
	width: 0px;
	height: 0px;
	margin: 0 auto;
	opacity: 0.8;
	-webkit-transform: rotate(180deg);
	        transform: rotate(180deg);
	border-left: 4px solid transparent;
	border-right: 4px solid transparent;
	border-bottom: 8px solid #000000c2;
}

.pinkPoint {
	width: 0px;
	height: 0px;
	margin: 0 auto;
	opacity: 0.8;
	-webkit-transform: rotate(180deg);
	        transform: rotate(180deg);
	border-left: 4px solid transparent;
	border-right: 4px solid transparent;
	border-bottom: 8px solid #ef4b81;
}

.zoom {
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
	transition: 0.2s;
}

.blur {
	/* opacity: 0.5;
	transition: 0.5s; */
	display: none;
}

@-webkit-keyframes width {
	0% {
		left: -110px;
	}
	50% {
		left: 50px;
	}
	100% {
		left: -110px;
	}
}

@keyframes width {
	0% {
		left: -110px;
	}
	50% {
		left: 50px;
	}
	100% {
		left: 0;
	}
}

.none {
	display: none;
}

.first {
	z-index: 999 !important;
}

.pageSlider-exit > .page {
	z-index: 1;
}

.pageSlider-enter > .page {
	z-index: 10;
}

.pageSlider-enter.left > .page {
	-webkit-transform: translate3d(100%, 0, 0);
	        transform: translate3d(100%, 0, 0);
}

.pageSlider-enter.right > .page {
	-webkit-transform: translate3d(-100%, 0, 0);
	        transform: translate3d(-100%, 0, 0);
}

.pageSlider-exit.left > .page {
	-webkit-transform: translate3d(-100%, 0, 0);
	        transform: translate3d(-100%, 0, 0);
}

.pageSlider-exit.right > .page {
	-webkit-transform: translate3d(100%, 0, 0);
	        transform: translate3d(100%, 0, 0);
}

.pageSlider-enter.pageSlider-enter-active > .page {
	-webkit-transform: translate3d(0, 0, 0);
	        transform: translate3d(0, 0, 0);
	transition: all 800ms ease-in-out;
}


